import {
  Paper,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TableFooter
} from '@material-ui/core'

import useConfig from '../hooks/useConfig'
import useCatalog from '../hooks/useCatalog'

function App() {
  const config = useConfig();
  const catalog = useCatalog();

  const tableHead = config.tableHead || [];

  const timestamp = catalog.timestamp || '';
  const rates = catalog.rates || [];

  return (
    <div className="App">
      <div>
        <img
          className="logo"
          src="https://www.clearviewsys.com/images/Clear-View-Systems-CVS-Logo.png" 
          alt="logo" 
        />
      </div>

      <Paper className="paper" elevation={3}>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                {tableHead.map(col => <TableCell key={col.key} align={col.align}>{col.title}</TableCell>)}
              </TableRow>
            </TableHead>

            <TableBody>
              {rates.filter(rate => rate.isFlagged).map(rate => (
                <TableRow key={rate.iso}>
                  {tableHead.map(col => (
                    <TableCell key={`${rate.iso}-${col.key}`} align={col.align}>{rate[col.key]}</TableCell>
                  ))}
                </TableRow>
              ))}
            </TableBody>

            <TableFooter>
              <TableRow>
                <TableCell colSpan={tableHead.length}>{timestamp}</TableCell>
              </TableRow>
            </TableFooter>
          </Table>
        </TableContainer>
      </Paper>
    </div>
  );
}

export default App;
