import { useState, useEffect } from 'react'
import fileDownload from 'js-file-download'
import {
  Paper,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Button
} from '@material-ui/core'
import { 
  AddCircle as AddCircleIcon,
  ArrowBack as ArrowBackIcon,
  Delete as DeleteIcon,
  ArrowForward as ArrowForwardIcon
} from '@material-ui/icons'
import styled from 'styled-components'

import useConfig from '../hooks/useConfig'

const PaperContainer = styled(Paper)`
  margin: 0.5rem;
  padding: 1rem;
`
const PaperCard = styled(Paper)`
  padding: 0.5rem;
  width: 10rem;
`
const AddColumnContainer = styled(Paper)`
  width: calc(10rem + 16px);
  height: 100%;
  min-height: 12rem;
  display: flex;
  justify-content: center;
  align-items: center;

  :hover {
    cursor: pointer;
    background-color: #f9f9f9;
  }
`
const IconHover = styled.div`
  width: 2rem;
  display: flex;
  justify-content: center;

  svg {
    ${props => props.display ? '' : 'display: none;'}
    :hover {
      cursor: pointer;
    }
  }
`

const DownloadButtonContainer = styled.div`
  padding: 0.5rem;
  display: flex;
  justify-content: center;
`

const Config = () => {
  const defaultConfig = useConfig();
  const [config, setConfig] = useState({});

  const tableColumns = config.tableHead || [];

  useEffect(() => {
    setConfig(defaultConfig)
  }, [defaultConfig]);

  const onColumnChange = (event, index, key) => {
    const tableHead = [...tableColumns]
    tableHead.splice(index, 1, { 
      ...tableColumns[index], 
      [key]: event.target.value 
    })
    setConfig({ ...config, tableHead })
  }

  const onColumnShift = (index, shift) => {
    const tableHead = [...tableColumns];
    const item = tableHead.splice(index, 1)
    tableHead.splice(index + shift, 0, item[0])
    setConfig({ ...config, tableHead })
  }

  const onColumnRemove = (index) => {
    const tableHead = [...tableColumns]
    tableHead.splice(index, 1)
    setConfig({ ...config, tableHead })
  }

  return (
    <div>
      <h1>Rate Board Configuration</h1>

      <PaperContainer elevation={3}>
        <Grid container direction="row" spacing={2}>
          
          {tableColumns.map((col, index) => (
            <Grid item>
              <PaperCard elevation={1}>
                <Grid container spacing={1} direction="column" justify="" alignItems="stretch">

                  <Grid item>
                    <Grid container direction="row" justify="space-between">
                      <Grid item>
                        <IconHover display={index !== 0}>
                          <ArrowBackIcon onClick={() => onColumnShift(index, -1)} />
                        </IconHover>
                      </Grid>
                      <Grid item>
                        <IconHover display>
                          <DeleteIcon onClick={() => onColumnRemove(index)} />
                        </IconHover>
                      </Grid>
                      <Grid item>
                        <IconHover display={index !== tableColumns.length - 1}>
                          <ArrowForwardIcon onClick={() => onColumnShift(index, 1)} />
                        </IconHover>
                      </Grid>
                    </Grid>
                  </Grid>

                  <Grid item>
                    <FormControl fullWidth>
                      <InputLabel id="field-select-label">Field</InputLabel>
                      <Select
                        labelId="field-select-label"
                        id="field-select"
                        value={col.key}
                        onChange={e => onColumnChange(e, index, 'key')}
                      >
                        <MenuItem value={'flag'}>Flag</MenuItem>
                        <MenuItem value={'iso'}>ISO</MenuItem>
                        <MenuItem value={'name'}>Name</MenuItem>
                        <MenuItem value={'country'}>Country</MenuItem>
                        <MenuItem value={'weBuy'}>We Buy</MenuItem>
                        <MenuItem value={'weSell'}>We Sell</MenuItem>
                        <MenuItem value={'invBuy'}>Inv Buy</MenuItem>
                        <MenuItem value={'invSell'}>Inv Sell</MenuItem>
                      </Select>
                    </FormControl>
                  </Grid>

                  <Grid item>
                    <TextField 
                      label="Field Name"
                      value={col.title}
                      onChange={e => onColumnChange(e, index, 'title')}
                    />
                  </Grid>

                  <Grid item>
                    <FormControl fullWidth>
                      <InputLabel id="align-select-label">Align</InputLabel>
                      <Select
                        labelId="align-select-label"
                        id="align-select"
                        value={col.align}
                        onChange={e => onColumnChange(e, index, 'align')}
                        defaultValue='left'
                      >
                        <MenuItem value={'center'}>Center</MenuItem>
                        <MenuItem value={'left'}>Left</MenuItem>
                        <MenuItem value={'right'}>Right</MenuItem>
                      </Select>
                    </FormControl>
                  </Grid>

                </Grid>
              </PaperCard>
            </Grid>
          ))}

          <Grid item>
            <AddColumnContainer onClick={() => setConfig({ 
                ...config, 
                tableHead: [...tableColumns, { title: '', key: '', align: 'left' }] 
              })} 
            >
              <AddCircleIcon fontSize="large" />
            </AddColumnContainer>
          </Grid>
            
        </Grid>
      </PaperContainer>

      <DownloadButtonContainer>
        <Button 
          variant="contained" 
          color="primary" 
          onClick={() => fileDownload(JSON.stringify(config), 'config.json')}
        >
          Download
        </Button>
      </DownloadButtonContainer>
    </div>
  )
}

export default Config